<!-- src/components/organisms/LoginModal.vue -->
<template>
  <transition name="fade">
    <div v-if="show" class="fullpage-login" tabindex="0" @keyup.enter="canSubmit && handleSubmit()">
      <div class="login-box">
        <!-- Âä®ÁîªÊ†áÈ¢òÔºàÁôΩËâ≤Ë¶ÜÂÜôËßÅ styleÔºâ -->
        <AnimatedTitle :text="currentTitle" class="dark-title" />

        <form @submit.prevent="handleSubmit" autocomplete="on">
          <!-- UID -->
          <FormGroup v-model:value="uid" placeholder="ËØ∑ËæìÂÖ• UID" :error-message="error && !storedCred ? error : ''" />

          <!-- ÂØÜÁ†Å -->
          <FormGroup v-model:value="password" type="password" :placeholder="isFirstLogin
            ? 'È¶ñÊ¨°ËÆæÁΩÆÂØÜÁ†ÅÔºàÂª∫ËÆÆËá≥Â∞ë 4 ‰ΩçÔºâ'
            : 'ËØ∑ËæìÂÖ•ÂØÜÁ†Å'" :autocomplete="isFirstLogin ? 'new-password' : 'current-password'"
            :error-message="error && storedCred ? error : ''" />

          <!-- ÈÄöÁî®ÈîôËØØ -->
          <ErrorMessage v-if="showGeneralErr" :message="error" />

          <!-- Êèê‰∫§ÊåâÈíÆ -->
          <Button type="submit" :disabled="!canSubmit" :loading="loading">
            {{ isFirstLogin ? 'ËÆæÁΩÆÂπ∂ÁôªÂΩï' : 'ÁôªÂΩï' }}
          </Button>
        </form>
      </div>
    </div>
  </transition>
</template>

<script setup>
/* ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ imports ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ */
import { getAllowedUids } from '@/config/auth'
import { pbkdf2Hash } from '@/utils/crypto'
import { computed, ref, watch } from 'vue'

import ErrorMessage from '@/components/atoms/ErrorMessage.vue'
import Button from '@/components/atoms/LoginButton.vue'
import AnimatedTitle from '@/components/molecules/AnimatedTitle.vue'
import FormGroup from '@/components/molecules/FormGroup.vue'

/* ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ props / emits ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ */
const props = defineProps({ show: Boolean })
const emit = defineEmits(['login-success'])

/* ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ state ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ */
const uid = ref('')
const password = ref('')
const currentTitle = ref('ÁôªÂΩï Captured Moments')
const error = ref('')
const loading = ref(false)
const bump = ref(0)         // ÊâãÂä®Ëß¶Âèë storedCred ÁöÑÈáçÊñ∞ËÆ°ÁÆó

/* ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ ËÆ°ÁÆóÂ±ûÊÄß ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ */
const credKey = computed(() => `cred_${uid.value.trim()}`)
const storedCred = computed(() => { bump.value; return safeJSON(localStorage.getItem(credKey.value)) })
const isFirstLogin = computed(() => uid.value.trim() !== '' && !storedCred.value)
const canSubmit = computed(() => {
  const u = uid.value.trim()
  const p = password.value
  return !!u && p.length >= (isFirstLogin.value ? 4 : 1) && !loading.value
})
const showGeneralErr = computed(() =>
  error.value && !(isFirstLogin.value || !storedCred.value)
)

/* ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ methods ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ */
function safeJSON(raw) {
  try { return JSON.parse(raw || 'null') }
  catch { return null }
}

function timingSafeEqual(a, b) {
  if (a.length !== b.length) return false
  let diff = 0
  for (let i = 0; i < a.length; i++) diff |= a.charCodeAt(i) ^ b.charCodeAt(i)
  return diff === 0
}

function createSalt() {
  return Array.from(crypto.getRandomValues(new Uint8Array(8)))
    .map(b => b.toString(16).padStart(2, '0')).join('')
}

async function handleSubmit() {
  if (!canSubmit.value) return
  const id = uid.value.trim()
  loading.value = true
  error.value = ''

  try {
    /* 1Ô∏è‚É£ Ë¥¶Âè∑ÂêàÊ≥ïÊÄß */
    const allowed = getAllowedUids()
    if (!allowed.includes(id)) { throw new Error('Áî®Êà∑‰∏çÂ≠òÂú®') }

    /* 2Ô∏è‚É£ È¶ñÊ¨° / Â∑≤Ê≥®ÂÜå‰∏§ÁßçÊµÅÁ®ã */
    if (!storedCred.value) {
      // ‚Äî‚Äî Á¨¨‰∏ÄÊ¨°ÔºöÈïøÂ∫¶Ê£ÄÊü• + ÂÜôÂÖ• localStorage ‚Äî‚Äî  
      if (password.value.length < 4) throw new Error('ÂØÜÁ†ÅËá≥Â∞ë 4 ‰Ωç')
      const salt = createSalt()
      const hash = await pbkdf2Hash(password.value, salt)
      localStorage.setItem(
        credKey.value,
        JSON.stringify({ hash, salt, iter: 100_000 })
      )
      bump.value++                               // ‚¨ÖÔ∏è Á´ãÂàªËß¶Âèë storedCred Êõ¥Êñ∞
    } else {
      // ‚Äî‚Äî Â∑≤Ê≥®ÂÜåÔºöËÆ°ÁÆó PBKDF2 ÂÜçÂØπÊØî ‚Äî‚Äî  
      const { hash, salt, iter = 100_000 } = storedCred.value
      const calc = await pbkdf2Hash(password.value, salt, iter)
      if (!timingSafeEqual(calc, hash)) throw new Error('ÂØÜÁ†Å‰∏çÊ≠£Á°ÆÔºåËØ∑ÈáçËØï')
    }

    /* 3Ô∏è‚É£ ÊàêÂäü ‚Üí ÂÜô session + emit */
    sessionStorage.setItem('currentUser', id)
    error.value = ''
    emit('login-success', id)
  } catch (e) {
    error.value = e.message || 'Á≥ªÁªüÈîôËØØ'
  } finally {
    loading.value = false
  }
}

/* ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ watch ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ */
watch(() => props.show, v => {
  if (v) {                           // ÊâìÂºÄÊó∂ÈáçÁΩÆË°®Âçï
    uid.value = ''
    password.value = ''
    error.value = ''
    loading.value = false
    bump.value++
  }
})
watch([uid, password], () => {
  if (error.value) error.value = ''                // ÊâìÂºÄÊó∂ÈáçÁΩÆÈîôËØØ
  error.value = ''
})
</script>

<style scoped>
.fullpage-login {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: #121212;
}

.login-box {
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
  padding: 24px;
  text-align: center;
  background: rgba(18, 18, 18, 0.9);
  backdrop-filter: blur(10px);
  border-radius: 12px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.55);
}

/* ËøõÂÖ•/Á¶ªÂºÄÂä®Áîª */
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}

/* Ë¶ÜÂÜô AnimatedTitle ÊñáÂ≠óËâ≤ */
.dark-title {
  color: #fff !important;
}
</style>
